<template>
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>등록하기</h2>
    </div>
    <div class="row marginbox">
      <h4 class="col-lg-4" align="center">제목</h4>
      <input
        type="title"
        class="form-control col-lg-8"
        name="title"
        placeholder="제목"
        v-model="Qna.title"
      />
    </div>
    <div class="row marginbox">
      <h4 class="col-lg-4" align="center">아이디</h4>
      {{LoginStatus.id}}
    </div>
    <div class="row marginbox">
      <h4 class="col-lg-4" align="center">내용</h4>
      <input
        type="contents"
        class="form-control col-lg-8"
        name="contents"
        placeholder="내용"
        v-model="Qna.contents"
      />
    </div>
    <input
      type="button"
      @click="addTodo"
      class="form-control col-lg-1 btn btn-primary"
      id="editButton"
      value="등록하기"
    />
  </div>
</template>
<script>
import Constant from "../Constant";
export default {
  data() {
    return {
      Qna: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      }
    };
  },
  computed: {
    LoginStatus() {
      return this.$store.state.loginStatus;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_LOGINSTATUS);
  },
  methods: {
    addTodo() {
      if (this.Qna.contents.trim() != "") {
        this.Qna.id = this.LoginStatus.id;
        console.log(this.Qna);
        this.$store.dispatch(Constant.ADD_Qna, { Qna: this.Qna });
        this.$router.push("/qna/list");
        this.clear();
      }
    },
    clear() {
      (this.Qna.title = ""), (this.Qna.regdate = ""), (this.Qna.contents = "");
    }
  }
};
</script>

