<template>
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>QnA 리스트</h2>
    </div>
    <table>
      <tr>
        <td>글 번호</td>
        <td>제목</td>
        <td>작성자</td>
        <td>작성일</td>
        <td>변경</td>
      </tr>
      <template v-for="qnaItem in QnaLists">
        <tr v-bind:key="qnaItem.no" @click="getQna(qnaItem.no)">
          <td>{{ qnaItem.no }}</td>
          <td>{{ qnaItem.title }}</td>
          <td>{{ qnaItem.id }}</td>
          <td>{{ qnaItem.regdate }}</td>
          <td>
            <!-- 자기 아이디로 작성한 글만 -->
            <template v-if="LoginStatus.status==true && LoginStatus.id ==  qnaItem.id">
              <button type="button" @click.stop="updateQna(qnaItem.no)">수정</button>
              <button type="button" @click.stop="removeQna(qnaItem.no)">삭제</button>
            </template>
          </td>
        </tr>
      </template>
    </table>

    <div class="searchCondition col-lg-2">
      <div align="center">
        <h4 class="title">
          <a>검색조건 &nbsp;&nbsp;&nbsp;</a>
        </h4>
        <select id="searchCondition" size="1" class="form-control" name="key" v-model="key">
          <option value="all" selected>검색조건</option>
          <option value="id">아이디</option>
          <option value="title">제목</option>
          <option value="contents">내용</option>
        </select>
      </div>
    </div>

    <div class="searchWords col-lg-2">
      <div align="center">
        <h4 class="title">
          <a>검색단어 &nbsp;&nbsp;</a>
        </h4>
        <input
          type="text"
          class="form-control"
          id="searchWord"
          name="word"
           v-model="word" 
        />
      </div>
    </div>

    <div class="searchButton col-lg-0.3">
      <div align="center">
        <h4 class="title">
          <a>&nbsp;&nbsp;&nbsp;</a>
        </h4>
        <button @click="search">검색</button>
      </div>
    </div>

 
</template>

<script>
import Constant from "../Constant";

export default {
  name: "QnaList",
  data() {
    return {
      key: "",
      word: ""
    };
  },
  computed: {
    QnaLists() {
      return this.$store.state.QnaLists;
    },
    LoginStatus() {
      return this.$store.state.loginStatus;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_QnaLIST);
    this.$store.dispatch(Constant.GET_LOGINSTATUS);
  },
  methods: {
    updateQna(no) {
      this.$router.push("/qna/update/" + no);
    },
    getQna(no) {
      this.$router.push("/qna/detail/" + no);
    },
    removeQna(no) {
      this.$store.dispatch(Constant.REMOVE_Qna, { no });
    },
    search() {
      this.$store.dispatch(Constant.SEARCH_Qna, {
        key: this.key,
        word: this.word
      });
    }
  }
};
</script>

<style scoped></style>
