<template>
  <div>
    <table>
      <th>
        <td>제목</td>
        <td>내용</td>
      </th>
      <tr>
        <td>
          <input type="text" placeholder="제목" v-model="Qna.title" />
        </td>
      </tr>
      <tr>
        <td>
          <input type="text" placeholder="내용입력" v-model="Qna.contents" />
        </td>
      </tr>
      <tr>
        <span @click="updateTodo">수정하기</span>
      </tr>

      <!-- <div type="text" placeholder="아이디" v-bind="Qna.id"></div> -->
    </table>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  name: "updatepage",
  data() {
    return {
      Qna: {
        no: 0,
        id: "",
        title: "",
        regdate: "",
        contents: ""
      }
    };
  },
  created() {
    this.$store
      .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
      .then(() => {
        this.Qna = { ...this.$store.state.Qna };
      });
  },
  methods: {
    updateTodo() {
      console.log(this.Qna);
      this.$store.dispatch(Constant.MODIEFY_Qna, { Qna: this.Qna });
      this.$router.push("/qna/list");
    }
  }
};
</script>

