<template>
  <!--
  <div class="container">
    <div class="section-header">
      <br />
      <br />
      <h2>상세정보</h2>
    </div>

    <div class="row marginbox">
      <h4 class="col-lg-4" align="center">아이디</h4>
      <input type="id" class="form-control col-lg-8" name="id" v-model="Qna.id" />
    </div>


  </div>-->
  <div style="text-align:center">
    <h2>상세정보</h2>
    아이디 : {{ Qna.id }}
    <br />
    번호 : {{ Qna.no }}
    <br />
    내용 : {{ Qna.contents }}
    <br />
    등록시간 : {{ Qna.regdate }}
    <br />새로운 댓글을 입력 :
    <input v-model="newComment.comment" />
    <button @click="addComment(Qna.id, Qna.no)">등록</button>
    <template v-if="CommentLists[0]!=null">
      <div v-for="comment in CommentLists" v-bind:key="comment.cno">
        {{ comment.id }} : {{ comment.comment }} : {{ comment.cregdate }}
        <template
          v-if="LoginStatus.status==true && LoginStatus.id ==  comment.id"
        >
          <button @click="deleteComment(comment.cno)">삭제</button>
        </template>
        <br />
      </div>
    </template>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  name: "detailpage",
  data() {
    return {
      Qna: {},
      CommentLists: [],
      newComment: {
        qno: 0,
        comment: "",
        id: ""
      }
    };
  },
  computed: {
    LoginStatus() {
      return this.$store.state.loginStatus;
    }
  },
  mounted() {
    this.getComments();
    this.getQnalist();
  },
  created() {
    this.newComment.qno = this.$route.params.no;
    this.$store.dispatch(Constant.GET_LOGINSTATUS);
  },
  methods: {
    getComments() {
      console.log("댓글들을 불러옵니다.");
      this.$store
        .dispatch(Constant.GET_Comments, { qno: this.newComment.qno })
        .then(() => {
          this.CommentLists = { ...this.$store.state.CommentLists };
          console.log("####################");
          console.log(this.CommentLists[0]);
        });
    },
    getQnalist() {
      this.$store
        .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
        .then(() => {
          this.Qna = { ...this.$store.state.Qna };
        });
      console.log("Aa" + this.$route.params.no);
    },
    addComment(id, qno) {
      if (this.newComment.comment.trim() != "") {
        this.newComment.qno = qno;
        this.newComment.id = id;
        console.log("id<<<<<" + qno);
        this.$store
          .dispatch(Constant.ADD_COMMENT, {
            qno: this.newComment.qno,
            id: this.newComment.id,
            comment: this.newComment.comment
          })
          .then(() => {
            console.log("##########");
            console.log(this.newComment.qno);
            this.getComments();
            this.clear();
          });
      }
    },
    deleteComment(no) {
      var cno = no;
      console.log("***" + this.Qna.no);
      this.$store
        .dispatch(Constant.REMOVE_COMMENT, { cno: cno, qno: this.Qna.no })
        .then(() => {
          this.getComments();
        });
    },
    clear() {
      this.newComment.comment = "";
    }
  }
};
</script>

<style scoped></style>
